.form {
  position: absolute;
  width: 100%;
  bottom: 0;
  left: 0;
  background-color: #dfe3e6;
  border-radius: 3px;
  height: auto;
  min-height: 32px;
  padding: 10px 0;

  & .textarea {
    background: #fff;
    border: none;
    box-shadow: inset 0 0 0 2px #0079bf;
    display: block;
    margin: 0;
    width: 100%;
    font-family: Helvetica Neue,Arial,Helvetica,sans-serif;
    font-size: 14px;
    font-weight: 400;
    color: #17394d;
    box-sizing: border-box;
    -webkit-appearance: none;
    border-radius: 3px;
    line-height: 20px;
    padding: 8px 12px;
  }
}